/* Linear gradient
(0)degree-->bottom to top
(90)degree-->left to right
(180)degree-->top to bottom*/

*{
  padding: 0;
  margin: 0;
  background-repeat: none;
  box-sizing: border-box;
  font-family:sans-serif;
}
div.main-div{
    width: 100vw;
    height: 100vh;
    background: linear-gradient(145deg, #6161db, #9797df);
    display: flex;
    justify-content: center;
    align-items: center;
}
div.center-div{
    width: 385px;
    height: 70vh;
    box-shadow: 0px 3px 13px 5px #3c488a;
    border-radius: 20px;
    background: #c9cfef;
}
h1{
    height: 50px;
    background: #7f46e5;
    text-align: center;
    color: white;
}
input{
    border: none;
    text-align: center;
    height: 30px;
    top: 10px;
    background: transparent;
    font-size: 20px;
    font-weight: 600;
    width: 60%;
    border-bottom: 4px solid #8566aa;
    outline: none;
    margin-left: 30px;
    margin-top: 14px;
    margin-right: 10px;
    margin-bottom: 30px;
}
button{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    font-weight: 600;
    font-size: 30px;
    color: white;
    background: #8354d6;
    cursor: pointer;
}
button:hover{
    background:rgb(102, 230, 102);
}
li{
    padding-left: 0px;
    text-align: center;
    font-size: 20px;
    font-weight: 600px;
    font-weight: 600;
    min-height: 40px;
    display: flex;
    align-items: center;
    color: #8566aa;
    text-transform: capitalize;
}
button.close{
    width: 30px;
    height: 30px;
    border-radius: 50%;
    font-weight: 600;
    font-size: 22px;
    color: white;
    background: #8354d6;
    cursor: pointer;
    margin-left: 20px;
    margin-right: 10px;
    margin-top: 4px;
}
button.close:hover{
    background: red;
}
div.todo{
    display: flex;
}